<template>
  <div class="shippingAddressList">
    <!-- <van-nav-bar class="task-nav-bar" ref="tabBar" title="我的收货地址" :border="false" left-arrow @click-left="goBack"
      :fixed="navBarFixed" /> -->
      <navigation-bar  @onLeftClick="goBack" pageName="我的收货地址" />
    <div class="shippingAddressListContent" ref="shippingAddressListContent" overflow-scroll="false" >
      <!-- <ion-refresher pulling-text="下拉刷新..." on-refresh="doRefresh()">
            </ion-refresher> -->
      <van-pull-refresh v-model="isLoading" @refresh="doRefresh">
        <!-- 订单详情入口进入 -->
        <div class="addressList" :style="{minHeight:addressListHeight+'px'}">
        <!-- <van-list class="addressList" v-model="loading" :finished="finished" style="width:100%;"> -->
          <div class="addressItem" v-for="(recAddrItem,recIndex) in recAddrListData" @click="setRecCode(recAddrItem)"
            :key="'recIndex'+recIndex">
            <img style="position: absolute;margin: 0 auto;width:38px;height:30px;top:0px;left:0px;"
              src="@/assets/images/mall/default_address.png" alt="" v-if="recAddrItem.isDefault==1">
            <div class="addressInfo">
              <div class="userInfo">
                <div :class="themeCurrent == '1' ? 'userName-l':'userName'">{{recAddrItem.recName}}</div>
                <div :class="themeCurrent == '1' ? 'userPhone-l' : 'userPhone'">{{recAddrItem.recPhone}}</div>
              </div>
              <img src="@/assets/images/mall/icon_edit.png" alt="" @click.stop="redactRess(recAddrItem)">
            </div>
            <div :class="themeCurrent == '1' ? 'addressTxt-l' : 'addressTxt'">地址：{{recAddrItem.recAddr}}</div>
          </div>
          <div class="noListBox" v-if="recAddrListData.length==0&&finished==true">
            <img class="noListImg" src="@/assets/images/default/no_address_img.png" alt="">
            <div class="noListTitle">暂无收货地址</div>
          </div>
        <!-- </van-list> -->
        </div>

        <div :style="placeholderStyle" v-show="recAddrListData.length!=0"></div>
      </van-pull-refresh>

    </div>
    <div class="addressBox" ref="addressBox">
      <div :class="themeCurrent == '1' ? 'addressBtn-l' : 'addressBtn'" @click="goToAddress()">新增收货地址</div>
    </div>
  </div>
</template>
<script>
  import { mapActions, mapState } from "vuex";
  import { Toast, Dialog } from "vant";
  import { storage } from '../../utils/storage'
  import { getRecAddrListService } from '../../services/shippingAddressService.js';
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  export default {
    name: 'shippingAddress',
    components: {NavigationBar },
    data() {
      return {
        recAddrListData: [],//地址列表数据
        flag: 1,//标记页面来源。1 正常入口 2 订单支付入口
        navBarFixed: true,
        isLoading: false,
        loading: false, //是否正在加载
        finished: false, //结束上拉加载
        immediate: false,
        padingtopNum :{'margin-top': this.$stbHeight +44+"px"},
        addressListHeight:0,
        themeCurrent: 0,
      }
    },
    created(){
      this.themeCurrent = this.$curTheme;
      this.getRecAddrList(); //获取地址列表
      this.$nextTick(()=>{
        this.addressListHeight = this.$refs.shippingAddressListContent.clientHeight-this.$stbHeight-44-this.$refs.addressBox.clientHeight-50;
      })
    },
    mounted() {
      this.flag = this.$route.query.flag;
      console.log(this.$route.query)
    },
    activated() {

    },
    computed: mapState({
      placeholderStyle: function () {
                let hv = this.$stbHeight + 44 + 75 + 'px !important'; //68px
                return { height: hv };
            },
    }),
    methods: {
      //下拉刷新
      doRefresh: function () {
        this.getRecAddrList();
      },
      //获取地址列表
      getRecAddrList: function () {

        var data = {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode:this.$userInfo.AREA_CODE,
          areaName:this.$userInfo.AREA_NAME
        }
        getRecAddrListService(data).then(res => {
          this.recAddrListData = res.data;
          this.isLoading = false;
          // this.loading = false;
          this.finished = true;
          // this.$forceUpdate();
        }).catch(error => {
        })
      },

      //订单支付入口 点击的那个地址选中  返回
      setRecCode: function (recAddrItem) {
        if(this.flag=="1"){
          return;
        }

        console.log(recAddrItem)
        storage.setStorage("recItem", recAddrItem, 999);
        this.goBack();
      },
      //编辑收货地址
      redactRess: function (recAddrItem) {
        console.log('---')
        if (this.flag == 1) {
          var recAddrData = {
            flag: 1,
            recAddrItem: recAddrItem
          }
        } else if (this.flag == 2) {
          //订单支付页
          var recAddrData = {
            flag: 3,
            recAddrItem: recAddrItem
          }
        }
        this.$router.push({ name: "setAddress", params: { data: recAddrData } });
      },
      //新增收货地址
      goToAddress: function () {
        if (this.flag == 1) {
          var recAddrData = {
            flag: 0
          }
        } else if (this.flag == 2) {
          //订单支付页
          var recAddrData = {
            flag: 2
          }
        }

        this.$router.push({ name: "setAddress", params: { data: recAddrData } });
      },
      goBack: function () {
        this.$router.back();
      },
    }
  }
</script>

<style lang="scss">

  .shippingAddressList {
    height: 100%;
    width: 100%;
    height: 100%;
    position: absolute;
  }

  .shippingAddressList .headerColor {
    background: #FFFFFF;
    box-shadow: 0 0 1px 0 rgba(100, 100, 100, 0.50);
  }

  .shippingAddressList .shippingAddressListContent {
    height: 100%;
    overflow: scroll;
    background-color: #F9F9F9;
    box-sizing: border-box;
  }

  .shippingAddressList .addressList {
    margin-top: 0.5PX;
    /* height: 100%; */
    padding: 20px 15px 16px;
    box-sizing: border-box;
  }

  .shippingAddressList .addressItem {
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 4px;
    padding: 20px;
    margin-bottom: 14px;
    position: relative;
    box-sizing: border-box;
  }

  .shippingAddressList .addressInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .shippingAddressList .userInfo {
    display: flex;
  }

  .shippingAddressList .userName {
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
  }

  .shippingAddressList .userName-l {
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
  }

  .shippingAddressList .userPhone {
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
    margin-left: 20px;
  }

  .shippingAddressList .userPhone-l {
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
    margin-left: 20px;
  }

  .shippingAddressList .addressInfo img {
    width: 18px;
    /* width: 14px;
    height: 14px; */
  }

  .shippingAddressList .addressTxt {
    font-family: PingFangSC-Regular;
    width: 80%;
    margin-top: 4px;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    line-height: 20px;
    word-break: break-all;
  }

  .shippingAddressList .addressTxt-l {
    font-family: PingFangSC-Regular;
    width: 80%;
    margin-top: 4px;
    font-size: 15px;
    color: #333333;
    letter-spacing: -0.04px;
    line-height: 20px;
    word-break: break-all;
  }

  .shippingAddressList .addressBox {
    width: 100%;
    position: fixed;
    bottom: 0px;
    height: 73px;
    background: #f9f9f9;
  }

  .shippingAddressList .addressBtn {
    font-family: PingFangSC-Regular;
    width: 320px;
    height: 44px;
    margin: 0 auto;
    background: #FB5768;
    box-shadow: 0 0 15px 0 rgba(197, 58, 72, 0.39);
    border-radius: 22px;
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: -0.05px;
    line-height: 44px;
    text-align: center;
  }

  .shippingAddressList .addressBtn-l {
    font-family: PingFangSC-Regular;
    width: 320px;
    height: 44px;
    margin: 0 auto;
    background: #FB5768;
    box-shadow: 0 0 15px 0 rgba(197, 58, 72, 0.39);
    border-radius: 22px;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: -0.05px;
    line-height: 44px;
    text-align: center;
  }

  .shippingAddressList .noListBox {
    width: 100%;
    height: 100%;
    padding-top: 125px;
    text-align: center;
  }

  .shippingAddressList .noListImg {
    width: 180px;
    height: 180px;
  }

  .shippingAddressList .noListTitle {
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #9A9A9A;
    letter-spacing: 0.06px;
    text-align: center;
  }
</style>
